.TeamPage {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;    
    grid-column-gap: 10px;
    grid-row-gap: 10px;
    
    .team-name-section {
        grid-column: span 3;
        justify-self: start;
        align-self: end;
    }
    .win-loss-section {
      grid-column: span 1;
      justify-self: center;
      padding: 40px;
      text-align: center;
        
    }

    .team-name {
        font-size: 4rem;
        margin-bottom: 20px;
    }

    .match-detail-section {
        grid-column: span 4;
    }

    .more-link{
         align-self: center;
         justify-self: center;
         font-size: 2rem;
    }




}